import React from 'react';
import { jsonSerializers } from '@moneybutton/json-api';

export default class Discussion extends React.Component {



  sendMessageHandler = () => {
    if (this.props.user) {
      this.props.postDiscussionMessage(document.getElementById('name').value);
      var message = encodeURIComponent(document.getElementById('name').value);

      // var urlParams = new URLSearchParams();
      // urlParams.append('data',['allaboardtest',message,this.props.user.username])
      var b64 = btoa(JSON.stringify({ type: 'postDiscussion', message, username: this.props.user.username }));
      var url = 'http://127.0.0.1:2000/q/' + b64;
      fetch(url)


      // var http = new XMLHttpRequest;
      // var url ='https://api.allaboard.cash/faucet/push';
      // var params = 'data='+JSON.stringify(['allaboardtest',"discussion",message,this.props.user.username]);
      // http.open('POST',url,true);
      // http.setRequestHeader('Content-type', 'application/x-www-form-urlencoded');
      // http.setRequestHeader('key', 'ZRfiqW9rdgDePXXeiT0mDEfYJgy8pwECnA');
      // http.send(params);
      document.getElementById('name').value = '';
    } else {
      this.props.loginHandler()
    }


  }

  returnKeyHandler = (e) => {
    if (e.keyCode == 13) {
      this.sendMessageHandler();
    }
  }

  componentDidMount() {
    document.getElementById('name').onkeypress = (e) => this.returnKeyHandler(e)
  }

  render() {
    var data = this.props.data;
    var discussionFeed = [];
    if (data) {
      for (let i = 0; i < data.length; i++) {
        if (data[i].type != 'discussion') {
          discussionFeed.push(<Action data={data[i]} changeQueryHandler={this.props.changeQueryHandler} />)
        } else {

          var message = data[i];
          if (this.props.user && message.username == this.props.user.username) {
            discussionFeed.push(<MessageRight data={message.message} />)

            var nextItem = data[i + 1];

            if (nextItem) {
              if (nextItem.type == 'discussion') {
                if (this.props.user && nextItem.username != this.props.user.username) {
                  discussionFeed.push(<UsernameRight data={this.props.user.username} />)
                }
              } else {
                discussionFeed.push(<UsernameRight data={this.props.user.username} />)
              }
            } else {
              discussionFeed.push(<UsernameRight data={this.props.user.username} />)
            }

          } else {
            discussionFeed.push(<MessageLeft data={message.message} />)

            var nextItem = data[i + 1];

            if (nextItem) {
              if (nextItem.type == 'discussion') {
                if (nextItem.username != message.username) {
                  discussionFeed.push(<UsernameLeft data={message.username} />)
                }
              } else {
                discussionFeed.push(<UsernameLeft data={message.username} />)
              }
            } else {
              discussionFeed.push(<UsernameLeft data={message.username} />)
            }


          }
        }
      }
    }


    return (
      <div data-w-id="a59d307d-9930-6dfd-5525-555df24d9f6e" style={{ display: "none" }} class="div-block-13">
        <div class="div-block-17">
          <div class="div-block-16">
            {discussionFeed}






          </div>
        </div>
        <div class="div-block-14">
          <div class="form-block-2 w-form">
            <form id="email-form" name="email-form" data-name="Email Form">
              <input autoComplete="off" type="text" class="text-field-2 w-input" maxlength="256" name="name" data-name="Name" id="name" />
            </form>

          </div>
          <div class="div-block-35" onClick={this.sendMessageHandler} >
            <div class="text-block-22">‚Üë</div>
          </div>
        </div>
      </div>
    );
  }
}

const MessageLeft = props => {
  return (
    <div class="discussion-post">
      <div class="text-block-12">{props.data}</div>
    </div>
  )
}
const MessageRight = props => {
  return (
    <div class="discussion-post discussion-post-right">
      <div class="text-block-12">{props.data}</div>
    </div>
  )
}

const UsernameLeft = props => (<div>
  <div class="discussion-user">{props.data}</div>
</div>)

const UsernameRight = props => (<div class="discussion-user discussion-user-right">{props.data}</div>)

const Action = props => {
  var vote = props.data.type == 'vote';
  var comment = props.data.type == 'comment';
  var post = !vote && !comment;
  var postTitle;
  var username;
  var action;
  var postTxid;

  if (vote) {
    username = "Someone";
    postTitle = props.data.votedOnTitle;
    action = 'voted on'
    postTxid = props.data.votedOn
  } else if (comment) {
    username = props.data.username;
    postTitle = props.data.rootPost.title;
    action = 'commented on'
    postTxid = props.data.rootPost.txid
  } else if (post) {
    username = props.data.username;
    postTitle = props.data.title;
    action = 'posted';
    postTxid = props.data._id
  }

  return (
    <div class="discussion-notification">
      <strong class="bold-text-4" onClick={() => {
        props.changeQueryHandler({ type: 'userFeed', userId: props.data.userId, contentType: 'post', sort: 'new', timePeriod: 'all time' });
        var windowWidth = window.$(window).width();
        if (windowWidth < 768) {
          document.getElementById('close-discussion').click();

        }
      }}
      >{username}</strong> {action} <strong class="bold-text-3" onClick={() => {
        props.changeQueryHandler({ type: 'post', post: postTxid })
        var windowWidth = window.$(window).width();
        if (windowWidth < 768) {
          document.getElementById('close-discussion').click();

        }
      }}
      >{postTitle}</strong></div>)

}