import React from 'react';
let MoneyButton = require('@moneybutton/react-money-button').default

function convertToHex(str) {
    var hex = '';
    for (var i = 0; i < str.length; i++) {
        hex += '' + str.charCodeAt(i).toString(16);
    }
    return hex;
}

export default class Alert extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            title: '',
            url: '',
            comment: ''

        }
    }

    closeAlert = () => {
        this.props.cancelReplyHandler();
        this.setState({ title: '', url: '', comment: '' })
    }

    onPaymentHandler = () => {
        document.getElementsByClassName('div-block-34')[0].click();
        this.closeAlert();
    }



    render() {
        var opreturn = ' '
        var reply = this.props.replyingTo;
        if (this.props.user && !reply) {
            opreturn = convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('post/vote') + ' ' + convertToHex('post') + ' ' + convertToHex(encodeURIComponent(this.state.title)) + ' ' + convertToHex(encodeURI(this.state.url)) + ' ' + convertToHex(this.props.user.username) + ' ' + convertToHex(this.props.user.id)
        } else if (this.props.user && reply) {
            opreturn = convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('comment') + ' ' + convertToHex(this.props.replyingTo._id) + ' ' + convertToHex(encodeURIComponent(this.state.comment)) + ' ' + convertToHex(this.props.user.username) + ' ' + convertToHex(this.props.user.id)
        }


        var username;

        if (reply) {
            username = reply.username
        }

        var postUI = (<div data-w-id="20261b05-15ea-fd10-4303-6a6538f195e4"
            //style="-webkit-transform:translate3d(0, 100VH, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0);-moz-transform:translate3d(0, 100VH, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0);-ms-transform:translate3d(0, 100VH, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0);transform:translate3d(0, 100VH, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(0) skew(0, 0)" 
            class="alert">
            <div class="alert-title">
                <div class="text-block-15">Submit a Link</div>
                <div data-w-id="c6b664e1-a7b1-959c-8ad1-97eaa9a9d996" onClick={this.closeAlert} class="div-block-29">
                    <div class="text-block-16">X</div>
                </div>
            </div>
            <div class="form-block-3 w-form">
                <form id="email-form-2" name="email-form-2" data-name="Email Form 2" class="form-2"><label for="node" class="field-label">Write a title</label>
                    <input onChange={() => this.setState({ title: document.getElementById('node').value })} value={this.state.title} type="text" maxlength="256" id="node" class="text-field-3 w-input" />
                    <label for="node-2" class="field-label-2">URL</label>
                    <input type="email" maxlength="256" id="node-2" class="text-field-3 w-input" value={this.state.url} onChange={() => this.setState({ url: document.getElementById('node-2').value })} /></form>

            </div>
            <div class="div-block-30">
                <div class='div-block-34' data-w-id="f78f9773-81a0-c31c-0a36-98ed96251b27" style={{ width: 0, height: 0 }}></div>
                <div class="submit-mb-container">
                    <MoneyButton
                        onPayment={this.onPaymentHandler}
                        outputs={[
                            {
                                amount: 0,
                                currency: 'BSV',
                                type: 'SCRIPT',
                                script: 'OP_RETURN ' + opreturn
                            }
                        ]}
                        label="Post"

                    />
                </div>
            </div>

        </div>)

        var commentUI = (<div data-w-id="20261b05-15ea-fd10-4303-6a6538f195e4" class="alert">
            <div class="alert-title">
                <div class="text-block-15">Comment</div>
                <div data-w-id="85e8fe7e-fd06-e878-870a-8f8994664ecb" onClick={this.closeAlert} class="div-block-29">
                    <div class="text-block-16">X</div>
                </div>
            </div>
            <div class="form-block-3 w-form">
                <form id="email-form-2" name="email-form-2" data-name="Email Form 2" class="form-2">
                    <label for="node-3" class="field-label-3">Responding to <strong>{username}</strong></label>
                    <textarea value={this.state.comment} onChange={() => this.setState({ comment: document.getElementById('field').value })} name="field" maxlength="5000" id="field" class="text-field-3 comment-field w-input"></textarea>
                </form>

            </div>
            <div class="div-block-30" >
                <div  data-w-id="f78f9773-81a0-c31c-0a36-98ed96251b27"  class='div-block-34' style={{ width: 0, height: 0}}></div>
                <div class="submit-mb-container" style={{ width: 140 }} >
                    <MoneyButton
                        onPayment={this.onPaymentHandler}
                        outputs={[
                            {
                                amount: 0,
                                currency: 'BSV',
                                type: 'SCRIPT',
                                script: 'OP_RETURN ' + opreturn
                            }
                        ]}
                        label="Reply"

                    />

                </div>
            </div>
            
        </div>)


        return (
            <div>
                <div data-w-id="45990265-012f-9c0a-8be9-a3e6027aab5b" style={{ display: 'none', opacity: "0" }} class="div-block-24"></div>
                <div data-w-id="b374f5a9-da8a-caa3-6b31-5447c2256b80" style={{ display: "none" }} class="div-block-25">
                    {reply ? commentUI : postUI}
                </div>
            </div>
        );
    }
}