import React from 'react';
let MoneyButton = require('@moneybutton/react-money-button').default

function timeSince(date) {

  var seconds = Math.floor((new Date() - date) / 1000);

  var interval = Math.floor(seconds / 31536000);

  if (interval > 1) {
    return interval + " years";
  }
  interval = Math.floor(seconds / 2592000);
  if (interval > 1) {
    return interval + " months";
  }
  interval = Math.floor(seconds / 86400);
  if (interval > 1) {
    return interval + " days";
  }
  interval = Math.floor(seconds / 3600);
  if (interval > 1) {
    return interval + " hours";
  }
  interval = Math.floor(seconds / 60);
  if (interval > 1) {
    return interval + " minutes";
  }
  return Math.floor(seconds) + " seconds";
}

export default class FeedComment extends React.Component {

  constructor(props) {
    super(props);
    var alreadyVoted = false
    if (props.user && props.data.votes.includes(this.props.user.id)) {
      alreadyVoted = true
    }
    this.state = {
      upvoteButtonClicked: false,
      alreadyVoted: alreadyVoted,
      voteTotal: props.data.voteTotal,

    }
  }
  upvoteClickHandler = () => {

    if (this.props.user) {
      var slidingViewId = '#' + this.props.data._id + 'slidingView';
      var upvoteButtonClicked = this.state.upvoteButtonClicked;
      var buttonId = '#' + this.props.data._id + 'button';

      if (upvoteButtonClicked) {
        window.$(slidingViewId).animate({ width: '0' }, 200, 'linear')
        window.$(buttonId).animate({ opacity: 0 }, 500, 'linear')

        this.setState({ upvoteButtonClicked: false })

      } else {
        window.$(slidingViewId).animate({ width: '170' }, 500, 'easeOutExpo')
        window.$(buttonId).animate({ opacity: 0.15 }, 500, 'linear')

        this.setState({ upvoteButtonClicked: true })

      }
    } else {
      this.props.loginHandler();
    }


  }
  upvoteHandler = () => {

    document.getElementById(this.props.data._id + 'button').click();
    this.setState({
      alreadyVoted: true,
      voteTotal: this.state.voteTotal += 0.05
    });

  }

  render() {

    var voteTotal = this.state.voteTotal.toString();
    if (voteTotal[voteTotal.length - 2] == '.') {
      voteTotal += '0'
    }
    var username = this.props.data.username;
    var selfPost = this.props.user && this.props.data.userId === this.props.user.id
    var buttonId = this.props.data._id + 'button';
    var slidingViewId = this.props.data._id + 'slidingView';
    var alreadyVoted = this.state.alreadyVoted;
    if (this.props.user) {
      alreadyVoted = this.state.alreadyVoted || this.props.data.votes.includes(this.props.user.id)
    }



    console.log(this.props.data)
    return (
      <div class="div-block-40">
        <div class="content-item">
          <div class="content-item-top">
            <div class="div-block-8">
              <div class="text-block-8">{'$' + voteTotal}</div>
              <div class="text-block-9" onClick={() => this.props.changeQueryHandler({ type: 'userFeed', userId: this.props.data.userId, contentType: 'post', sort: 'new', timePeriod: 'all time' })}>{username}</div>
              <div class="text-block-6">{timeSince(this.props.data.timestamp * 1000) + ' ago'}</div>
            </div>
            <div class="content-item-content content-item-content-comment">
              <div class="text-block-24">in reply to <strong class="bold-text-5" onClick={() => this.props.changeQueryHandler({ type: 'post', post: this.props.data.rootPost.txid })} >{this.props.data.rootPost.title}</strong></div>
              <div class="comment-content">{this.props.data.comment}</div>
            </div>
          </div>
          <div class="div-block-10">
            <div class="div-block-11">
              {selfPost ? null : <div class="action-bar-button">
                <div class="post-action">{alreadyVoted ? "üó≥Ô∏è‚úîÔ∏è" : "Upvote"}</div>
              </div>}
              <div class="action-bar-button" onClick={() => this.props.replyHandler(this.props.data)} >
                <div class="post-action">Reply</div>
              </div>
              <div class="action-bar-button" onClick={() => this.props.changeQueryHandler({ type: 'post', post: this.props.data.rootPost.txid })}>
                <div class="post-action">Show Thread</div>
              </div>
            </div>
          </div>
        </div>

        {selfPost ? null : <div style={{ backgroundColor: 'black', opacity: 0 }} onClick={this.state.alreadyVoted ? null : this.upvoteClickHandler} id={buttonId} class="div-block-44"></div>}
        <div style={{ width: "0PX" }} id={slidingViewId} class="div-block-41">
          <div class="div-block-42">
            <div class="div-block-43">
              {this.props.user ? <MoneyButton
                label='upvote'
                onPayment={this.upvoteHandler}
                to='112'
                amount='0.05'
                currency='USD'

              // outputs={[
              //   {
              //     amount: 0,
              //     currency: 'BSV',
              //     type: 'SCRIPT',
              //     script: 'OP_RETURN ' + convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('post/vote') + ' ' + convertToHex('vote') + ' ' + convertToHex(this.props.data._id) + ' ' + convertToHex(this.props.user.id)

              //   }, {
              //     to: this.props.data.transaction.in[0].e.a,
              //     amount: "0.05",
              //     currency: 'USD',


              //   }

              // ]}


              /> : null}
            </div>
          </div>
        </div>
      </div>
    );
  }
}