import React from 'react';

const ProfileStats = (props)=>{
  var amountEarned = props.data.amountEarned.toString();
         if(amountEarned[amountEarned.length-2]=='.'){
           amountEarned+='0'
         }
  return(
    <div class="content-item content-item-profile">
                <div class="div-block-31">
                  <div class="text-block-17">{props.data.username}</div>
                  <div class="div-block-32">
                    <div class="profile-stat">
                      <div class="text-block-19">Posts</div>
                      <div class="text-block-18">{props.data.numPosts}</div>
                    </div>
                    <div class="profile-stat">
                      <div class="text-block-19">Comments</div>
                      <div class="text-block-18">{props.data.numComments}</div>
                    </div>
                    <div class="profile-stat profile-stat-right">
                      <div class="text-block-19">Votes</div>
                      <div class="text-block-18">{props.data.numVotes}</div>
                    </div>
                  </div>
                </div>
                <div class="div-block-33">
                  <div class="text-block-20">Earned</div>
                  <div class="text-block-21">{'$'+amountEarned}</div>
                </div>
              </div>
);}

export default ProfileStats;