import React from 'react'
import $script from 'scriptjs';

export default class SortAndFiler extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      query: this.props.query
    }


  }

  changeQuery = (queryChange) => {
    var sort = queryChange.contentType;
    var by  = queryChange.sort;
    var from = queryChange.timePeriod;

    if(sort){
      document.getElementById("sort").click();
    } else if (by){
      document.getElementById("by").click();

    } else if (from){
      document.getElementById("from").click();

    }


    var newQuery = {
      ...this.props.query,
      ...queryChange
    }

    this.setState({
      query: newQuery
    })

    this.props.changeSortAndFilter(newQuery);
  }

  inputChangeHandler=()=>{
    this.changeQuery({
      search:document.getElementById('name-3').value
    })
  }

 


  render() {
    var sort;
    var by;
    var from;

    

    this.props.query.contentType == 'comment' ? sort = 'Comments' : sort = 'Posts';
    this.props.query.sort == 'top' ? by = 'Top' : this.props.query.sort == 'new' ? by = 'New' : by = 'Hot';
    switch(this.props.query.timePeriod){
      case 'day':
        from = 'Day';
        break;
      case 'week':
        from = 'Week';
        break;
      case 'month':
        from = 'Month';
        break;
      case 'all time':
        from='All Time';
        break;
    }

    var latestSearchQuery;
    JSON.parse(this.props.latestQuery).search?latestSearchQuery=JSON.parse(this.props.latestQuery).search:latestSearchQuery=''


    return (
      <div style={{ width: '100%' }} >
        <div class="w-form">
          <form id="email-form-3" name="email-form-3" data-name="Email Form 3" >
            <input type="text" autoComplete="off" value={latestSearchQuery} class="text-field-5 w-input" maxlength="256" name="name-3" data-name="Name 3" placeholder="Search" id="name-3" onChange={this.inputChangeHandler} />
          </form>
          
        </div>
        <div class="content-item parameters" style={{ width: '100%' }} >
          <div class="parameters-container">
            <div class="parameters-item">
              <div class="parameter-text parameter-item">Sort:</div>
            </div>
            <div class="dropdown-container">
              <div data-w-id="83c3eaae-bf1d-1928-0b11-31907b7cf3d6" id='sort' class="dropdown-trigger">
                <div class="parameter-text">{sort}</div>
              </div>
              <div style={{ height: '0PX', display: "none" }} class="dropdown-content">
                <div data-w-id="3a2f1878-9d77-fb16-e68d-1dfe48353203" class="dropdown-item" onClick={() => this.changeQuery({ contentType: 'post' })}>
                  <div class="parameter-text">Posts</div>
                </div>
                <div data-w-id="2d9b06ef-22af-1a47-a5b5-2fc05b211be4" class="dropdown-item" onClick={() => this.changeQuery({ contentType: 'comment' })} >
                  <div class="parameter-text">Comments</div>
                </div>
              </div>
            </div>
            <div class="parameters-item">
              <div class="parameter-text parameter-item">by:</div>
            </div>
            <div class="parameters-item parameters-item-dropdown">
              <div class="dropdown-container">
                <div data-w-id="4534be0a-5b81-6bc3-37f7-50b03c2e0e9b" id='by' class="dropdown-trigger">
                  <div class="parameter-text">{by}</div>
                </div>
                <div style={{ height: '0PX', display: "none" }} class="dropdown-content">
                  <div data-w-id="4534be0a-5b81-6bc3-37f7-50b03c2e0e9f" class="dropdown-item" onClick={() => this.changeQuery({ sort: 'hot' })}>
                    <div class="parameter-text">Hot</div>
                  </div>
                  <div data-w-id="eab1e4cf-2441-cd8d-fbb9-a8635a7bc9d3" class="dropdown-item" onClick={() => this.changeQuery({ sort: 'top' })}>
                    <div class="parameter-text">Top</div>
                  </div>
                  <div data-w-id="4534be0a-5b81-6bc3-37f7-50b03c2e0ea2" class="dropdown-item" onClick={() => this.changeQuery({ sort: 'new' })}>
                    <div class="parameter-text">New</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="parameters-container parameters-container-right">
            <div class="parameters-item">
              <div class="parameter-text parameter-item">From:</div>
            </div>
            <div class="parameters-item parameters-item-dropdown">
              <div class="dropdown-container dropdown-container-right">
                <div data-w-id="4f7095d4-3b82-1424-67bc-d51c84655b1d" id='from' class="dropdown-trigger">
                  <div class="parameter-text">{from}</div>
                </div>
                <div class="dropdown-content dropdown-content-right" style={{ height: '0PX', display: "none" }} >
                  <div data-w-id="4f7095d4-3b82-1424-67bc-d51c84655b21" class="dropdown-item" onClick={() => this.changeQuery({ timePeriod: 'day' })}>
                    <div class="parameter-text">Day</div>
                  </div>
                  <div data-w-id="4f7095d4-3b82-1424-67bc-d51c84655b24" class="dropdown-item" onClick={() => this.changeQuery({ timePeriod: 'week' })}>
                    <div class="parameter-text">Week</div>
                  </div>
                  <div data-w-id="4f7095d4-3b82-1424-67bc-d51c84655b24" class="dropdown-item" onClick={() => this.changeQuery({ timePeriod: 'month' })}>
                    <div class="parameter-text">Month</div>
                  </div>
                  <div data-w-id="4f7095d4-3b82-1424-67bc-d51c84655b27" class="dropdown-item" onClick={() => this.changeQuery({ timePeriod: 'all time' })}>
                    <div class="parameter-text">All Time</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

}

